@font-face {
  font-family: "Helvetica-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("./fonts/Helvetica-Bold.otf") format("opentype");
}
@font-face {
  font-family: "Helvetica-Light";
  font-style: normal;
  font-weight: 300;
  src: url("./fonts/Helvetica Light.ttf") format("truetype");
}
@font-face {
  font-family: "Helvetica-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/Helvetica Regular.otf") format("opentype");
}
@font-face {
  font-family: "Proxima Nova-Regular";
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/Mark Simonson - Proxima Nova A.otf") format("opentype");
}
@font-face {
  font-family: "Proxima Nova-Semibold";
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/.173.otf") format("opentype");
}
@font-face {
  font-family: "Proxima Nova-Bold";
  font-style: normal;
  font-weight: 700;
  src: url("./fonts/Mark Simonson  Proxima Nova Bold.otf") format("opentype");
}

.screen a {
  display: contents;
  text-decoration: none;
}

button{
  background-color: transparent;
  border: none;
  background: transparent;
}
input{
  background-color: transparent;
  border: 0;
  background: transparent;
}
textarea{
  background-color: transparent;
  border: 0;
  background: transparent;
}

.container-center-horizontal {
  display: flex;
  flex-direction: row;
  justify-content: center;
  pointer-events: none;
  width: 100%;
}

.container-center-horizontal > * {
  flex-shrink: 0;
  pointer-events: auto;
}

.valign-text-middle {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.video-background{
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1000;
  overflow: hidden;
}

#video{
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -1000;
  transform: translateX(-50%) translateY(-50%);
}

* {
  box-sizing: border-box;
}

.lightning-balance-text {
  left: 0;
  letter-spacing: 0;
  line-height: 17.2px;
  opacity: 0.9;
  text-align: center;
  white-space: nowrap;
}

.button {
  height: 21px;
  margin-bottom: 11.0px;
  width: 18px;
}

.share-icon {
  height: 18px;
  width: 18px;
}

.share-button-text {
  align-self: center;
  letter-spacing: 0;
  line-height: 16.2px;
  margin-bottom: 2.0px;
  min-height: 14px;
  text-align: center;
  white-space: nowrap;
  width: 43px;
}

.copy-link-icon {
  height: 15px;
  margin-bottom: 0;
  width: 14px;
}

.copy-link-text {
  align-self: flex-end;
  letter-spacing: 0;
  line-height: 16.2px;
  min-width: 68px;
  text-align: center;
  white-space: nowrap;
}

.refferals-icon {
  height: 18px;
  margin-bottom: -0.75px;
  margin-left: -1px;
  width: 20px;
}

.refferals-title {
  letter-spacing: 1.04px;
  line-height: 26px;
  margin-left: 5px;
  text-shadow: 0px 0px 0.07px #ffffff;
  white-space: nowrap;
  width: 115px;
}

