/* Шрифты */
:root { 
  --alto: #d9d9d9;
  --black: #000000;
  --black-2: #00000030;
  --blueberry: #627fff80;
  --eerie-black: #1a1a1a;
  --frostbite: #df36a6;
  --havelock-blue: #4094da;
  --kashmir-blue: #476395;
  --lilac: #cd95ce30;
  --nobel: #b3b3b3;
  --quill-gray: #d4d4d4;
  --silver: #bdbdbd;
  --white: #ffffff;
  --white-2: #ffffffcc;
  --white-3: #ffffffb2;
 
  --font-size-l: 14px;
  --font-size-m: 12px;
  --font-size-xl: 15px;
  --font-size-xxl: 16px;
  --font-size-xxxl: 17px;
  --font-size-xxxxl: 19px;
  --font-size-xxxxxl: 22px;
 
  --font-family-helvetica-bold: "Helvetica-Bold", Helvetica;
  --font-family-helvetica-light: "Helvetica-Light", Helvetica;
